<template>
  <div>
    <button
      @click="toggleList"
      class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button custom-button--right custom-button--primary"
    >
      {{buttonName}}
    </button>
    <keep-alive>
      <transition name="component-fade" mode="out-in">
        <data-table
          :columns="columns"
          :data="salesOrders.data"
          :actionMethod="fetchSalesOrders"
          :count="salesOrders.count"
          :searchPlaceholder="'Search by Product Name'"
          v-if="showList"
        ></data-table>
        <sales-order-view v-else @completed="toggleList"></sales-order-view>
      </transition>
    </keep-alive>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import { DataTable } from 'SHARED/data_table'
import { SalesOrderView } from '.'
export default {
  name: 'production-list',
  components: {
    DataTable,
    SalesOrderView
  },
  computed: {
    ...mapGetters({
      salesOrders: 'getSalesOrders'
    }),
    buttonName () {
      if (this.showList) {
        return 'Add'
      } else {
        return 'Back to List'
      }
    }
  },
  methods: {
    ...mapActions([
      'fetchSalesOrders'
    ]),
    toggleList () {
      this.showList = !this.showList
    }
  },
  data () {
    return {
      showList: true,
      columns: [
        {
          field: 'name',
          label: 'Client Name',
          type: String
        },
        {
          field: 'total_incl_tax',
          label: 'Total Amount',
          type: String
        },
        {
          field: 'procurement',
          label: 'Procurement',
          type: String
        },
        {
          field: 'pending_amount',
          label: 'Pending Amount',
          type: String
        },
        {
          field: 'status',
          label: 'Status',
          type: String
        }
      ]
    }
  }
}
</script>



